.slider {
  position: relative;
}

.slider__button {
  height: 48px;
  width: 48px;
  background-color: #fff;
  border: 1px solid #c9c9c9;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: auto 0;
  font-size: 18px;
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
}

@media (hover: hover) {
  .slider__button {
    display: flex;
  }
}

.slider__button--hidden {
  display: none;
}

.slider__button--next {
  right: 0;
}

.slider__button--prev {
  left: 0;
}

.slider__wrapper {
  overscroll-behavior-x: contain;
  -ms-overflow-style: none;
  scrollbar-width: none;
  scroll-snap-type: x proximity;
  flex-direction: row;
  display: flex;
  overflow-x: auto;
}

.slider__wrapper::-webkit-scrollbar {
  display: none;
}

@media (hover: hover) {
  .slider__wrapper {
    scroll-snap-type: initial;
  }
}

.slider__wrapper:not(.is-dragging) {
  scroll-behavior: smooth;
}

.slider__wrapper.is-scrollable {
  cursor: move;
}

.slider__wrapper.is-dragging {
  cursor: grabbing;
  -webkit-user-select: none;
  user-select: none;
}

.slider__wrapper__slide {
  scroll-snap-align: start;
}

